import React, { useEffect, useState } from "react";
import ReactSpeedometer from "react-d3-speedometer";
import { io } from "socket.io-client";

interface DashboardData {
  timestamp: number;
  speed: number;
  temperature: number;
  voltage: number;
  soc: number;
  wh: number; // remaining energy in Wh
}

export default function Dashboard() {
  const [data, setData] = useState<DashboardData>({
    timestamp: 0,
    speed: 0,
    temperature: 0,
    voltage: 0,
    soc: 0,
    wh: 0,
  });

  useEffect(() => {
    // const evt = new EventSource("http://127.0.0.1:5000/data");
    // evt.onmessage = (e) => setData(JSON.parse(e.data));
    // return () => evt.close();
    // OLD SSE DATA TRANSFER IF WE EVEN NEED IT
    const socket = io("http://127.0.0.1:5000");
    socket.on("telemetry", (msg: DashboardData) => setData(msg));
    return () => {
      socket.disconnect();
    };
  }, []);

  const batteryColor =
    data.soc > 60 ? "limegreen" : data.soc > 30 ? "gold" : "red";

  const tempColor =
    data.temperature < 20
      ? "deepskyblue"
      : data.temperature <= 60
      ? "limegreen"
      : "red";

  return (
    <>
      {/* Main dashboard layout */}
      <div className="dashboard-main">
        {/* Left: Speedometer */}
        <div className="dashboard-left">
          <ReactSpeedometer
            maxValue={100}
            value={data.speed}
            needleColor={data.speed > 90 ? "red" : "white"}
            startColor="green"
            endColor="red"
            segments={10}
            ringWidth={40}
            textColor="white"
            height={350}
            width={500}
          />
          <h2 style={{ color: "white", marginTop: "1rem" }}>
            {data.speed.toFixed(1)} km/h
          </h2>
        </div>

        {/* Right: Battery + Telemetry */}
        <div className="dashboard-right">
          {/* Battery Section */}
          <div className="dashboard-section">
            <h2 style={{ color: "white" }}>Battery</h2>
            <div
              style={{
                width: "220px",
                height: "28px",
                background: "#333",
                borderRadius: "8px",
                overflow: "hidden",
              }}
            >
              <div
                style={{
                  width: `${data.soc}%`,
                  height: "100%",
                  background: batteryColor,
                  transition: "width 0.3s, background 0.3s",
                }}
              />
            </div>
            <p
              style={{
                color: batteryColor,
                fontSize: "1.2rem",
                margin: "0.3rem",
              }}
            >
              {data.soc.toFixed(1)} % SoC
            </p>
            <p style={{ color: "#ccc", fontSize: "1rem", marginTop: "0" }}>
              {data.wh.toFixed(0)} Wh remaining
            </p>
          </div>

          {/* Temperature Section */}
          <div className="dashboard-section">
            <h2 style={{ color: tempColor }}>
              Temperature: {data.temperature.toFixed(1)} C
            </h2>
          </div>

          {/* Voltage Section */}
          <div className="dashboard-section">
            <h3 style={{ color: "white", marginBottom: "0.3rem" }}>
              Voltage:{" "}
              <span style={{ color: "cyan" }}>{data.voltage.toFixed(2)} V</span>
            </h3>
          </div>
        </div>
      </div>

      {/* Footer info bar */}
      <div className="dashboard-footer">
        <span>Timestamp: {Math.round(data.timestamp / 1000)} s</span>
      </div>
    </>
  );
}
